.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container{
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #c1c1c1;
}

.cont{
  height: 80vh;
  width: 25%;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0px 10px 10px 4px #7ceee8;
}

h1{
  text-align: center;
  background: linear-gradient(to right, #833ab4, #fd1d1d, #fcb045);
  color: #fff;
}

input{
  text-align: center;
  height: 40px;
  margin-left: 20px;
  border: none;
  border-bottom: 4px solid #833ab4;
  font-size: 17px;
}

input:focus{
  outline: none;
}

.add-btn{
  all: unset;
  background-color: #833ab4;
  color: #fff;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  text-align: center;
  margin-left: 15px;
  font-size: 24px;
  font-weight: bolder;
}

.add-btn:hover{
  background-color: green;
}

ol li{
  list-style: none;
  font-size: 20px;
  margin-top: 8px;
  color: #833ab4;
  font-weight: 600;
}

.cross{
  all: unset;
  width: 22px;
  text-align: center;
  border-radius: 50%;
  height: 22px;
  font-size: 10px;
  background-color: #833ab4;
  color: white;
}

.cross:hover{
  background-color: rgb(223, 55, 55);
}

@media screen and (max-width:700px) {
  .cont{
    width: 75%;
  }
  
}

*{
  font-family: Ubuntu;
}
